import React, { Component } from "react";
import unit from "ethjs-unit";
import Countdown from "react-countdown-now";
var ip = require("ip");

import TokenSale from "../build/contracts/TokenSale.json";
import ERC20 from "../build/contracts/ERC20.json";
import * as constants from "./constants.js";
import getWeb3 from "./utils/getWeb3";

//Styles
import "basscss/css/basscss.css";
import "basscss-btn/index.css";
import "basscss-btn-primary/index.css";
import "basscss-forms/index.css";
import "basscss-progress/index.css";
import "./css/fonts.css";
import "./App.css";

//Images
import logo from "./assets/frlogo.png";

// Renderer callback with condition
const CountryBannedMessage = ({ banned }) =>
  banned ? (
    <span className="center highlight">
      <p>Your country is not currently allowed to purchase tokens!</p>
    </span>
  ) : (
    <span />
  );

const Completionist = () => <span>Token Sale Has Begun!</span>;
const renderer = ({ days, hours, minutes, seconds, completed }) => {
  if (completed) {
    // Render a completed state
    return <Completionist />;
  } else {
    // Render a countdown
    return (
      <div className="clearfix">
        <h2 className="secondary center bold mb2">Sale Starts In</h2>
        <div className="col col-12 ">
          <div className="col col-3 flex flex-column center">
            <span className="col h1 highlight bold">{days}</span>
            <span className="col">days</span>
          </div>
          <div className="col col-3 flex flex-column center">
            <span className="col h1 highlight bold">{hours}</span>
            <span className="col">hours</span>
          </div>
          <div className="col col-3 flex flex-column center">
            <span className="h1 highlight bold">{minutes}</span>
            <span>minutes</span>
          </div>
          <div className="col col-3 flex flex-column center">
            <span className="h1 highlight bold">{seconds}</span>
            <span>seconds</span>
          </div>
        </div>
      </div>
    );
  }
};

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      eth: 0,
      tokenSaleCap: 0,
      tokenSaleRaised: 0,
      tokenSaleRemainingEggs: 0,
      eggBalance: 0,
      remaining: 0,
      percentSold: 0.0,
      priceInGwei: constants.PRICE_IN_GWEI,
      countryBan: true,
      tokenSale: null,
      account: null,
      web3: null,
      transactionHash: null
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentWillMount() {
    // Get network provider and web3 instance.
    // See utils/getWeb3 for more info.

    getWeb3
      .then(results => {
        this.setState({
          web3: results.web3
        });

        // Instantiate contract once web3 provided.
        this.instantiateContract();
      })
      .catch((e) => {
        console.log("Error finding web3.");
        console.log(e);
      });
  }

  instantiateContract() {
    // const contract = require("truffle-contract");
    // const tokenSale = contract(TokenSale);
    const appLoop = this.appLoop.bind(this);
    // tokenSale.setProvider(this.state.web3.currentProvider);
    const tokenSale = this.state.web3.eth
      .contract(TokenSale.abi)
      .at(constants.TOKEN_SALE_ADDRESS);

    this.setState({
      tokenSale: tokenSale
    }, function() {
      appLoop();
      setInterval(function() {
        appLoop();
      }, 10000);
    });
  }

  appLoop() {
    this.checkCountry();
    const setState = this.setState.bind(this);
    this.state.tokenSale.cap(function(error, value) {
      setState({
        tokenSaleCap: value.toNumber()
      })
    })

    this.state.tokenSale.weiRaised(function (error, value) {
      setState({
        tokenSaleRaised: value.toNumber()
      });
    });

    this.setState({ account: this.state.web3.eth.accounts[0] });
    this.getTokenBalance();
    this.getRemaining();
  }

  getTokenBalance() {
    const erc20 = this.state.web3.eth
      .contract(ERC20.abi)
      .at(constants.TOKEN_ADDRESS);
    const setState = this.setState.bind(this);

    erc20.balanceOf(
      this.state.account,
      function(error, result) {
        const balance = unit.fromWei(result, "ether");
        setState({ eggBalance: parseFloat(balance) });
      }
    );

    erc20.balanceOf(this.state.tokenSale.address, function(error, result) {
      const balance = unit.fromWei(result, "ether");
      setState({ tokenSaleRemainingEggs: parseFloat(balance) });
    });
  }

  getRemaining() {
    const erc20 = this.state.web3.eth
      .contract(ERC20.abi)
      .at(constants.TOKEN_ADDRESS);
    const setState = this.setState.bind(this);

    erc20.balanceOf(this.state.tokenSale.address, function(error, result) {
      const balance = unit.fromWei(result, "ether");
      const percent = (1 - balance / constants.INITIAL_SUPPLY).toFixed(4);

      setState({
        remainingEggs: balance,
        percentSold: percent
      });
    });
  }

  checkCountry() {
    //var address = '193.23.122.134';
    console.log('country check');
    fetch('https://json.geoiplookup.io/')
    .then(response => response.json())
    .then(data => {

      var address = data.ip;
      console.log(address);
      const setState = this.setState.bind(this);
      fetch("https://api.ip2country.info/ip?" + address)
        .then(response => response.json())
        .then(data => {
          const countryCode = data.countryCode3;
          const banned = constants.BANNED_COUNTRY_CODES.includes(countryCode);
          setState({
            countryBan: banned
          });
        });

    })
    .catch(e => {
      console.log(e);
    });
  }

  handleChange(event) {
    if (event.target.name === "tokens") {
      this.setState({
        eth: parseFloat(event.target.value * this.state.priceInGwei)
      });
    } else {
      this.setState({
        [event.target.name]: event.target.value
      });
    }
  }

  handleSubmit(event) {
    this.buyTokens();
    event.preventDefault();
  }

  buyTokens() {
    const setState = this.setState.bind(this);
    if (this.state.eth <= 0.0 || this.state.eth === undefined) {
      alert("Please enter a value greater than 0.");
      return;
    } else if(this.state.access_code !== constants.EARLY_ACCESS_CODE) {
      alert("Please enter the correct early access code.");
      return;
    } else {
      alert(this.state.eth);
      const ethValue = this.state.eth;
      this.state.tokenSale
        .buyTokens(this.state.account, {
          from: this.state.account,
          gas: 3000000,
          value: ethValue
        }, function(error, tx) {
          if(tx) {
            console.log(tx);
            /// TODO: send to GA
            setState({
              transactionHash: tx
            }, function () {
              alert('Thank you for supporting us! Be sure Make sure to add this token contract address to your wallet, to see your  EGGtokens. The steps to add your token depends on the browser and wallet you are using.  Questions, comments or concerns, please contact us in our Telegram channel.');
            });
          }
          if(error) {
            console.log(error);
          }
        });
    }
  }

  render() {
    return (
      <div>
        <div className="overflow-auto navbar py2 px4 ">
          <div
            id="navbar-content"
            className="overflow-auto max-width-3 mx-auto"
          >
            <div className="navitem left">
              <img src={logo} width="157px" height="47" alt="Free Range" />
            </div>
            <div className="navitem right mt2">
              <span className="">My EGG Tokens:</span>{" "}
              {this.state.eggBalance.toLocaleString()}
            </div>
          </div>
        </div>

        <div id="Hero" className="hero justify-center mx-auto my0">
          <h1 className="center p4 m0">TOKEN PRE-SALE</h1>
        </div>

        <div id="Main-Container" className="max-width-3 mx-auto px2">
          <div className="clearfix my3">
            <div id="content" className="sm-col sm-col-7 md-col-7 lg-col-7 px2">
              <div id="countdown" className="pb3">
                <Countdown
                  date={Date.parse("18 July 2018 00:12:00 GMT")}
                  renderer={renderer}
                />
              </div>

              <div id="SaleDetails" className="clearfix my2">
                <div className="col col-6 flex flex-column left">
                  <span className="secondary">Tokens Sold</span>{" "}
                  {unit
                    .fromWei(this.state.tokenSaleRaised, "ether")
                    .toLocaleString()}{" "}
                  ETH
                </div>

                <div className="col col-6 flex flex-column right right-align">
                  <span className="secondary">Pre-Sale Hard Cap</span>{" "}
                  {unit
                    .fromWei(this.state.tokenSaleCap, "ether")
                    .toLocaleString()}{" "}
                  ETH
                </div>
                <progress
                  value={this.state.percentSold}
                  className="progress my2"
                >
                  0.5
                </progress>
                <div className="center">
                  {(this.state.percentSold * 100).toFixed(1)}%{" "}
                  <span className="secondary">Pre-Sale Sold</span>
                </div>
                <div className="h2 center mt3">
                  <span className="highlight">1 EGG = </span>
                  <span id="strikethru">
                    <span className="highlight">0.000072 </span>
                  </span>
                  <span className="highlight pl1"> 0.000036 ETH</span>
                </div>
                <div className="h2 center mt3" style={{fontSize: '14px'}}>
                  <a target="_BLANK" href='https://etherscan.io/token/0x7c47318dd2d79976032951f535cb5a713bd53971'>0x7c47318dd2d79976032951f535cb5a713bd53971</a>
                </div>
              </div>
            </div>
            <div id="sidebar" className="sm-col sm-col-5 md-col-5 lg-col-5 px2">
              <div id="buy-tokens-form" className="clearfix p3 my1 rounded">
                <form className="" onSubmit={this.handleSubmit}>
                  <label className=" h6 secondary">Early Access Code:</label>
                  <br />
                  <input
                    name="access_code"
                    type="text"
                    className="input mrn3"
                    onChange={this.handleChange}
                  />
                  <br />
                  <span className="clearfix mb1">
                    <span className="h4 secondary">Wallet Address: </span>
                    <br />
                    <span className="h2">
                      {(this.state.account || "0x00000000").slice(0, 10)}...
                    </span>
                  </span>
                  <br />

                  <label className="h4 secondary">EGG Tokens to Buy:</label>
                  <br />
                  <input
                    name="tokens"
                    type="text"
                    className="input"
                    onChange={this.handleChange}
                    placeholder="0"
                  />
                  <br />

                  <span className="clearfix mb1">
                    <span className="h5 secondary">Total ETH Cost: </span>
                    <br />
                    <span className="h3">
                      {unit.fromWei(this.state.eth || 0, "ether")} ETH
                    </span>
                  </span>
                  <br />

                  <div className="center">
                    <input
                      type="button"
                      value="Buy Tokens"
                      className="btn btn-primary center"
                      disabled={this.state.countryBan}
                      onClick={this.handleSubmit}
                    />
                    <br />
                  </div>

                  <CountryBannedMessage banned={this.state.countryBan} />
                  <p className="center light-grey">Why can’t I buy Tokens?*</p>
                </form>
              </div>
            </div>
          </div>
          <hr />
          <section id="footer" className="pt1 mb2 clearfix">
            <p id="page" className="light-grey">
              *Persons from the following countries are unable to purchase
              EGGtokens directly through the DApp: (US, China, Crimera, Cuba,
              Iran, North Korea, Syria, Sudan). US persons may acquire EGGtokens
              through <a href="#">StartEngine.com/FRtoken</a>
            </p>
          </section>
        </div>
      </div>
    );
  }
}

export default App;



